  mutations: {
    setComment (state, { id, comment }) {
      const allVotes = state.responses[id].votes
      const myVoteResults = allVotes.filter(vote => {
        return vote.user === state.name
      })

      if (myVoteResults.length === 0) {
        const vote = {
          vote: undefined,
          user: state.name,
          comment: comment
        }
        Vue.set(allVotes, allVotes.length, vote)
      } else {
        // Vue.set(myVote, 'comment', comment)
        const myVote = myVoteResults[0]
        myVote.comment = comment
      }
    },
    setVote (state, { id, vote }) {
      const allVotes = state.responses[id].votes
      const myVoteResults = allVotes.filter(vote => {
        return vote.user === state.name
      })

      if (myVoteResults.length === 0) {
        const voteObj = {
          vote: vote,
          user: state.name,
          comment: undefined
        }
        Vue.set(allVotes, allVotes.length, voteObj)
      } else {
        // Vue.set(myVote, 'comment', comment)
        const myVote = myVoteResults[0]
        myVote.vote = vote
      }
    },
    toggleQuestionVisibility (state, question) {
      // window.console.log(`toggle ${id}, ${state.hiddenQuestions.entries()}`)
      // window.console.debug(state.hiddenQuestions)

      const internalSet = new Set(state.hiddenQuestions)
      const item = question.id

      if (internalSet.has(item)) {
        internalSet.delete(item)
      } else {
        internalSet.add(item)
      }

      state.hiddenQuestions = [...internalSet]
    },
    jumpBackward (state) {
      // window.console.log(this)
      if (!this.getters.canJumpBackward) {
        return false
      }

      state.currentResponseId--
    },
    jumpForward (state) {
      // window.console.log(this)
      if (!this.getters.canJumpForward) {
        return false
      }

      state.currentResponseId++
    }
  },
  actions: {
  },
  modules: {
  },
  getters: {
    canJumpBackward (state) {
      // window.console.log(state)
      const ids = Object.keys(state.responses)
      const index = ids.indexOf(String(state.currentResponseId))
      const nextIndex = index - 1

      return nextIndex >= 0
    },
    canJumpForward (state) {
      const ids = Object.keys(state.responses)
      // window.console.log(state)
      // window.console.log(ids)
      const index = ids.indexOf(String(state.currentResponseId))
      if (index === -1) {
        return false
      }

      const nextIndex = index + 1

      return nextIndex < ids.length
    },
    isQuestionVisible (state) {
      return function (question) {
        const item = question.id
        const internalSet = new Set(state.hiddenQuestions)
        return !internalSet.has(item)
      }
    },
    getMyComment (state) {
      const myVote = state.responses[state.currentResponseId].votes.filter(resp => {
        return resp.user === state.name
      })

      if (myVote.length === 0) {
        return ''
      }

      return myVote[0].comment || ''
    },
    getMyVote (state) {
      const myVote = state.responses[state.currentResponseId].votes.filter(resp => {
        return resp.user === state.name
      })

      if (myVote.length === 0) {
        return undefined
      }

      return myVote[0].vote || ''
    }
  }
