<template>
  <div>

    <div class="container response">
      <div class="move-panel">
        <a @click="jumpBackward" v-bind:class="{ disable: !canJumpBackward }">🡄 jump to previous</a> |
        <a @click="jumpForward" v-bind:class="{ disable: !canJumpForward }">jump to 🡆</a>
      </div>

      <table class="table table-striped">
        <tr v-for="item in response.response"
             v-bind:key="item.id"
             v-show="isQuestionVisible(item)"
            class="row text-left">

          <td class="col col-sm-auto" @click="toggleQuestionVisibility(item)">
            <img src="../assets/no-eye.png" class="oko">
          </td>

          <td class="col question">{{ item.question }}</td>
          <td class="col answer">{{ item.answer }}</td>
        </tr>
      </table>

      <small @click="toggleHiddenQuestions()">
        <a href="#">
      <span v-if="areHiddenQuestionsVisible">
        hide (click to <img src="../assets/eye.png" class="oko"> to display)
      </span>
      <span v-else>
        show <span>{{ hiddenQuestions.length }}</span>
      </span>
      hidden questions</a></small>
      <ul v-show="areHiddenQuestionsVisible">
        <li v-for="item in response.response"
            v-bind:key="item.id"
            v-show="!isQuestionVisible(item)"
        >
          <img src="../assets/eye.png"
               @click="toggleQuestionVisibility(item)"
               class="oko">
          {{ item.question }}</li>
      </ul>

      <hr />
        <!--
        <div class="row">
          <div class="col">
            <span class="btn"
                  v-bind:class="isMyVote('ano') ? 'btn-success' : 'btn-outline-success'"
                  @click="doVote('ano')"
            >ano</span>
            <span class="btn" v-bind:class="isMyVote('substitute') ? 'btn-warning' : 'btn-outline-warning'">náhradník</span>
            <span class="btn" v-bind:class="isMyVote('ne') ? 'btn-danger' : 'btn-outline-danger'">ne</span>
          </div>
        </div>

        <div class="row">
          <div class="col">
            <span class="btn btn-outline-dark">hlasovat příště, teď ne</span>
          </div>
        </div>

      <hr />
      -->

      <div>
        Votes:<br />
        <select v-model="vote">
          <option v-for="(item, id) in voteOptions" v-bind:key="id">{{ item }}</option>
        </select>
        <input v-model="comment">
        <ul>
          <li v-for="item in getVotesVithoutMine" v-bind:key="item.id">
            <span class="author">{{ item.user }}</span>
            <span class="vote" v-bind:class="item.vote">({{ item.vote }})</span>
            <span class="comment">{{ item.comment }}</span>
          </li>
        </ul>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'ResponseDetail',
  data () {
    return {
      areHiddenQuestionsVisible: false
    }
  },
  methods: {
    jumpBackward () {
      if (this.canJumpBackward) {
        this.$store.commit('jumpBackward')
      }
    },
    jumpForward () {
      if (this.canJumpForward) {
        this.$store.commit('jumpForward')
      }
    },
    toggleQuestionVisibility (item) {
      this.$store.commit('toggleQuestionVisibility', item)
    },
    isQuestionVisible (item) {
      return this.$store.getters.isQuestionVisible(item)
    },
    toggleHiddenQuestions () {
      this.areHiddenQuestionsVisible = !this.areHiddenQuestionsVisible
    }
  },
  props: {
    value: {
      get () {
        window.console.log('this.response')
        return this.response
      },
      set (newValue) {
        window.console.log('hodn')
        window.console.log(newValue)
      }
    }
  },
  computed: {
    response () {
      return this.$store.state.responses[this.value]
    },
    name: {
      get () {
        return this.$store.state.name
      }
    },
    voteOptions: {
      get () {
        return this.$store.state.voteOptions
      }
    },
    comment: {
      get () {
        return this.$store.getters.getMyComment
      },
      set (newValue) {
        const payload = {
          id: this.value,
          comment: newValue
        }
        this.$store.commit('setComment', payload)
      }
    },
    vote: {
      get () {
        return this.$store.getters.getMyVote
      },
      set (newValue) {
        const payload = {
          id: this.value,
          vote: newValue
        }
        this.$store.commit('setVote', payload)
      }
    },
    isMyVote () {
      return (vote) => {
        const currnetUserVote = this.response.votes[this.user]?.vote
        return vote === currnetUserVote
      }
    },
    canJumpBackward () {
      return this.$store.getters.canJumpBackward
    },
    canJumpForward () {
      return this.$store.getters.canJumpForward
    },
    hiddenQuestions () {
      // console.log(this.$store.state.hiddenQuestions)
      return this.$store.state.hiddenQuestions
    },
    getVotesVithoutMine () {
      return this.response.votes
      // return this.response.votes.filter(vote => {
      //   return vote.user !== this.name
      // })
    }
  }
}
</script>

<style scoped>
.move-panel .disable {
  opacity: 0.4;
}

.author {
  font-weight: bold;
}

img.oko, a {
  cursor: pointer;
}

.vote {
  padding: 2px 5px;
}
.vote.yes {
  background-color: lime;
}
.vote.no {
  background-color: red;
}

.comment {
  font-style: italic;
}
</style>
